body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header{
  background-color: black;
  color: white;
  padding: 20px;
  text-align: center;
}
.Scoreboard{
  
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: yellow;
  height: 100px;
  width: 100px;
  top: 15px;
  left: 80vw;
  position: relative;
}
.Revealer{
  height: 60px;
  width: 60px;
  left: 45vw;
  background-color: green;
  border: 1px solid black;
  border-radius: 50%;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Container{
  display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 25px;
    height: 65vh;display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 25px;
    height: 65vh;
}
main{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 25px;
  height: 65vh;
  
}
input{
  padding: 10px;
  width: 40px;
  outline: none;
  border: 1px solid black;
}
button{
  padding: 8px;
  background-color: black;
  border-radius: 3px;
  color: white;
}
.Suggestionbox{
  background-color: green;
  padding: 10px;
  border-radius: 2px;
  color: white;
}
